@import url("https://fonts.googleapis.com/css?family=Roboto:400,700");
* {
  font-family: 'Roboto', sans-serif;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
body {
  margin: 0;
  background: #eee;
}
.content {
  width: calc(100% - 336px);
  position: absolute;
  margin-left: 300px;
  padding: 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}
.sidebar {
  -webkit-box-shadow: 3px 0 3px rgba(0,0,0,0.1);
          box-shadow: 3px 0 3px rgba(0,0,0,0.1);
  position: fixed;
  width: 300px;
  background: #fff;
  height: 100%;
  overflow: auto;
}
.sidebar::-webkit-scrollbar {
  width: 2px;
}
.sidebar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 rgba(0,0,0,0);
}
.sidebar::-webkit-scrollbar-thumb {
  background-color: transparent;
}
.sidebar li {
  list-style-type: none;
  padding: 16px;
  margin: 0 8px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.sidebar li span {
  vertical-align: middle;
}
.sidebar li div {
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background: #3f51b5;
  display: inline-block;
  vertical-align: middle;
}
.sidebar li.active {
  background: #f6f7fc;
}
.sidebar li:hover {
  background: rgba(0,0,0,0.05);
}
.card {
  margin: 16px;
  background: #fff;
  -webkit-box-shadow: 0 3px 2px rgba(0,0,0,0.1);
          box-shadow: 0 3px 2px rgba(0,0,0,0.1);
  border-radius: 4px;
  padding: 16px;
  min-width: 40%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.card h4 {
  font-size: 16px;
  margin: 0;
  color: #3f51b5;
  padding: 8px;
  display: inline-block;
  background: #f6f7fc;
  text-transform: uppercase;
}
.card .actions {
  margin: 0 4px;
  text-align: right;
}
.card .actions .button {
  font-weight: bold;
  font-size: 14px;
  color: #fff;
  background: #3f51b5;
  padding: 10px;
  border-radius: 4px;
  display: inline-block;
  margin-top: 16px;
  cursor: pointer;
}
.card .actions .button:hover {
  background: #3949a3;
}
.result-container {
  margin-top: 16px;
}
.result {
  color: #fff;
  background: #363636;
  padding: 16px;
  margin-top: 16px;
}
.time {
  margin-left: 8px;
  padding: 8px;
  color: #3f51b5;
  border-radius: 2px;
  border: 2px solid #f6f7fc;
}
.part {
  background: #606060;
  border-radius: 8px;
  padding: 8px;
  display: inline-block;
  margin-right: 8px;
}



